<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";
import "element-ui/lib/theme-chalk/display.css";
import { FunctionalCalendar } from "vue-functional-calendar";

export default {
  name: "home",
  components: {
    HelloWorld,
    FunctionalCalendar
  },
  data() {
    return {
      form: {
        name: "",
        date1: "",
        desc: ""
      },
      calendarData: {},
      drawer: false,
      allEvents: [
        {
          id: 1,
          content: "fuck"
        },
        {
          id: 2,
          content: "fuck2"
        }
      ]
    };
  },
  computed: {
    selectedDate() {
      return this.calendarData.selectedDate;
    }
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  },
  watch: {
    // calendarData: {
    //   handler(val) {
    //     console.log(val);
    //   },
    //   deep: true
    // }
    selectedDate(val) {
      // 改变date format：若月份和日期是个位数，则加入前导零
      let temp = val.split("-");
      for (let i = 1; i < temp.length; ++i) {
        if (temp[i].length == 1) {
          temp[i] = "0" + temp[i];
        }
      }
      let fval = temp[0] + "-" + temp[1] + "-" + temp[2];
      this.$http.get('/api/contract.php' + '?' + 'due=' + fval)
      .then((res)=>{
        console.log(res);
        this.allEvents = res.data;
      })
    }
  },
};
</script>


